import React, { useState, useEffect } from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import './App.css';

function App() {
  const [mode, setMode] = useState(1);
  const [input, setInput] = useState("");
  const [input2, setInput2] = useState("");
  const [results, setResults] = useState([]);
  const [isCopied, setIsCopied] = useState(false);

  const percentages = [20, 3, 5, 8, 10, 15, 20, 25, 50, 75, 80, 85, 90, 95];

  const handleChange = (event, inputNumber) => {
    let value = event.target.value.replace(',', '.');
    if (value === "") {
      if (inputNumber === 1) {
        setInput("");
      } else {
        setInput2("");
      }
    } else if (!isNaN(parseFloat(value))) {
      if (inputNumber === 1) {
        setInput(value);
      } else {
        setInput2(value);
      }
    }
  };

  useEffect(() => {
    let newResults;
    if (mode === 1 && input !== "") { // Добавил проверку на наличие значения в input
      newResults = percentages.map(percent => ({
        percent,
        value: ((parseFloat(input) * percent) / 100).toFixed(2).replace('.', ',')
      }));
    } else if (mode === 2 && input !== "" && input2 !== "") { // Добавил проверку на наличие значений в input и input2
      newResults = [{
        percent: input2,
        value: ((parseFloat(input) * parseFloat(input2)) / 100).toFixed(2).replace('.', ',')
      }];
    }
    setResults(newResults || []);
  }, [input, input2, mode]);


  const handleKeyDown = (event, inputNumber) => {
    let value = inputNumber === 1 ? input : input2;
    value = value.replace(',', '.');
    if (!isNaN(parseFloat(value))) {
      if (event.key === 'ArrowUp') {
        event.preventDefault();
        value = (parseFloat(value) + 1).toString();
      } else if (event.key === 'ArrowDown' && parseFloat(value) > 0) {
        event.preventDefault();
        value = (parseFloat(value) - 1).toString();
      }
      if (inputNumber === 1) {
        setInput(value);
      } else {
        setInput2(value);
      }
      // Здесь можно вызвать handleChange, чтобы обновить results
      handleChange({target: {value}}, inputNumber);
    }
  };


  const handleCopy = () => {
    setIsCopied(true);
    setTimeout(() => setIsCopied(false), 2000); 
  };

  const switchMode = () => {
    setMode(mode === 1 ? 2 : 1);
    setInput("");
    setInput2("");
    setResults([]);
  };

  return (
    <div className="app-container">
      <button className="button" onClick={switchMode}>
        {mode === 1 ? "Mode 2" : "Mode 1"}
      </button>
        <input 
          className="input-field" 
          type="text" 
          onChange={(e) => handleChange(e, 1)} 
          onKeyDown={(e) => handleKeyDown(e, 1)}
          value={input} 
          placeholder="Enter Number" 
        />
        {mode === 2 && (
          <input 
            className="input-field" 
            type="text" 
            onChange={(e) => handleChange(e, 2)} 
            onKeyDown={(e) => handleKeyDown(e, 2)}
            value={input2} 
            placeholder="Enter Percent" 
          />
        )}

      {results.map((result, index) => (
        <div key={index}>
          <p className="result">{result.percent}%: {result.value}</p>
          <CopyToClipboard text={result.value}>
            <button className="copy-button" onClick={handleCopy}>Copy</button>
          </CopyToClipboard>
        </div>
      ))}
      {isCopied ? <p style={{color: 'green'}}>Copied!</p> : null}
    </div>
  );
}

export default App;
